[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important; }

td, th {
  padding: 5px;
  line-height: 1.3em; }
  td [type="checkbox"] + label, th [type="checkbox"] + label {
    top: 5px; }
  td.added, th.added {
    min-width: 120px; }
  td .description, th .description {
    margin: 5px;
    padding: 10px 0px;
    font-size: 0.9em;
    position: relative; }
    td .description::before, th .description::before {
      content: 'Beschreibung';
      position: absolute;
      width: 100%;
      height: 1px;
      max-width: 100%;
      left: 0px;
      font-size: 0.6em;
      line-height: 1em;
      text-decoration: underline;
      font-style: italic; }
    td .description::before, th .description::before {
      top: 0px; }
